import React, { FC, ReactNode } from "react";
import cn from "classnames";

import styles from "./Th.module.scss";

export interface IThProps {
  children: ReactNode;
  className?: string;
  colspan?: number;
  rowspan?: number;
}

export const Th: FC<IThProps> = ({ children, className, colspan, rowspan }) => {
  return (
    <th
      className={cn(styles.Th, className)}
      colSpan={colspan}
      rowSpan={rowspan}
    >
      {children}
    </th>
  );
};
