import React from "react";
import get from "lodash/get";

import { FormControl } from "components/Forms/FormControl";
import { Label } from "components/Forms/Label";
import { Select, Option } from "components/Forms/Select";
import { Visible } from "components/Visible";
import * as Filter from "components/Filter";

import { useQueryFilter } from "hooks/useQueryFilter";

interface IMerchantProps {
  merchants: string[];
  placements: any;
}

export const Merchant = (props: IMerchantProps) => {
  const { placements } = props;
  const queryFilter = useQueryFilter();
  const merchants: string[] = get(props, "merchants", []);
  const merchant: string =  queryFilter.merchant;
  const placement = queryFilter.placement;
  const handleMerchantChange = (evt: React.FormEvent<HTMLSelectElement>) => {
    const { currentTarget } = evt;
    queryFilter.handleMerchant(currentTarget?.value);
  };

  const handlePlacementChange = (evt: React.FormEvent<HTMLSelectElement>) => {
    const { currentTarget } = evt;
    queryFilter.handlePlacement(currentTarget?.value);
  };

  return (
    <>
      <Visible if={merchants.length > 1}>
        <Filter.Merchant>
          <FormControl>
            <Label>Merchant</Label>
            <Select value={merchant} onChange={handleMerchantChange}>
              <Option value="">All</Option>
              {merchants.map(({ id, name }: any) => (
                <Option key={`${id}-${name}`} value={id}>
                  {name}
                </Option>
              ))}
            </Select>
          </FormControl>
        </Filter.Merchant>
      </Visible>

      <Visible if={placements.length}>
        <Filter.Placement>
          <FormControl>
            <Label>Placement</Label>
            <Select value={placement} onChange={handlePlacementChange}>
              <Option value="">All</Option>
              {placements.map(({ placement }: any) => (
                <Option key={placement} value={placement}>
                  {placement}
                </Option>
              ))}
            </Select>
          </FormControl>
        </Filter.Placement>
      </Visible>
    </>
  );
};
