import { useState } from "react";
import isFunction from "lodash/isFunction";

export const useSortable = (callback: (field: string, order: number) => void, initial: any = {}) => {
  const [fields, setFields] = useState(initial);

  const handleSort = (field: string) => {
    setFields((prev: { [prop: string]: number }) => {
      const next = prev[field] ? { [field]: prev[field] * -1 } : { [field]: 1 };
      const order = next[field];

      if (isFunction(callback)) callback(field, order);

      return next;
    });
  };

  return [fields, handleSort];
};
