import React, { FC, useRef } from "react";

import * as Modal from "components/Modal";
import { Portal } from "components/Portal";
import { Visible } from "components/Visible";

import { useOnClickOutside } from "hooks/useOnClickOutside";
import { useToggle } from "hooks/useToggle";

import { UpdateUserModalForm } from "./UpdateUserModalForm";
export interface IUpdateUserModal {
  user: any;
  render: ({
    visible,
    onClick,
  }: {
    visible: boolean;
    onClick: () => void;
  }) => any;
}

export const UpdateUserModal: FC<IUpdateUserModal> = (props) => {
  const modalRef = useRef();
  const { user, render } = props;
  const [showModal, handleToggleModal] = useToggle();

  useOnClickOutside(modalRef, () => {
    if (showModal) {
      handleToggleModal();
    }
  });

  return (
    <>
      {render({ visible: showModal, onClick: handleToggleModal })}
      <Visible if={showModal}>
        <Portal>
          <Modal.Overlay>
            <Modal.Content ref={modalRef}>
              <UpdateUserModalForm
                user={user}
                handleClose={handleToggleModal}
              />
            </Modal.Content>
          </Modal.Overlay>
        </Portal>
      </Visible>
    </>
  );
};
