import React, { FC, createContext, useContext } from "react";
import { RangeService } from "services/RangeService";

export const Context = createContext(null as any)

export const RangeProvider: FC = ({ children }) => {

  return <Context.Provider value={new RangeService()}>{children}</Context.Provider>;
};

export const useRange = (): RangeService => useContext(Context);
