import * as numbers from 'utils/numbers';

export const percents = (value: number, total: number) => {
  try {
    return numbers.toMoney((100 * value) / total);
  } catch (err) {
    return 0;
  }
};

export const round = (value: number) => {
  try {
    return numbers.toMoney(value);
  } catch (err) {
    return 0;
  }
};
