import React, { FC } from "react";
import { Link as RouterLink, LinkProps } from "react-router-dom";
import cn from "classnames";
import isString from "lodash/isString";

import styles from "./Link.module.scss";

export interface ILinkProps extends LinkProps {
  button?: boolean;
}

export interface ILinkButtonProps extends LinkProps {
  button?: boolean;
  variant?: any;
}

export const Link: FC<ILinkProps> = ({ children, to, button, ...props }) => {
  const className = cn(styles.link, props.className, {
    [styles.button]: button,
  });

  if (isString(to) && (to.startsWith("https://") || to.startsWith("http://"))) {
    return (
      <a
        {...props}
        href={to}
        className={className}
        target="_blank"
        rel="noopener noreferrer"
      >
        {children}
      </a>
    );
  }

  return (
    <RouterLink {...props} to={to} className={className}>
      {children}
    </RouterLink>
  );
};

export const Button: FC<ILinkButtonProps> = ({
  children,
  variant = "defaults",
  ...props
}) => {
  return (
    <Link {...props} className={cn(styles[variant])} button>
      {children}
    </Link>
  );
};
