import React from "react";

import { Container } from "components/pages/Order/Container";
import { Header } from "components/pages/Order/Header";
import { Title } from "components/pages/Order/Title";
import { Back } from "components/pages/Order/Back";
import { Table, Caption, Tr, Th, Td } from "components/pages/Order/Table";
import { Tbody } from "components/Table";

function fallback(value: any, defaults = " - ") {
  return value ?? defaults;
}

export const Panel = ({ order }: any) => {
  return (
    <Container>
      <Header>
        <Back />
        <Title>{order?.id}</Title>
      </Header>

      <Table>
        <Caption>Initial data</Caption>
        <Tbody>
          <Tr>
            <Th>Merchant name</Th>
            <Th>Created at </Th>
            <Th>Crypto wallet</Th>
          </Tr>
          <Tr>
            <Td>{fallback(order?.initialData?.merchantName)}</Td>
            <Td>{fallback(order?.initialData?.createdAt)} </Td>
            <Td>{fallback(order?.initialData?.cryptoWallet)} </Td>
          </Tr>
          <Tr>
            <Th>Email</Th>
            <Th>Last update</Th>
            <Th>User Id</Th>
          </Tr>
          <Tr>
            <Td>{fallback(order?.initialData?.email)}</Td>
            <Td>{fallback(order?.initialData?.updatedAt)}</Td>
            <Td>{fallback(order?.initialData?.userId)}</Td>
          </Tr>
          <Tr>
            <Th>Fiat amount</Th>
            <Th>Crypto amount</Th>
            <Th>Order exchange rate</Th>
          </Tr>
          <Tr>
            <Td>
              {order?.initialData?.amountFiat?.amount}{" "}
              {order?.initialData?.amountFiat?.currency}
            </Td>
            <Td>
              {order?.initialData?.amountCrypto?.amount}{" "}
              {order?.initialData?.amountCrypto?.currency}
            </Td>
            <Td>{fallback(order?.initialData?.orderExchangeRate)}</Td>
          </Tr>
        </Tbody>
      </Table>

      <Table>
        <Caption>Verification data</Caption>
        <Tbody>
          <Tr>
            <Th>Country</Th>
            <Th>State </Th>
            <Th>Crypto wallet</Th>
          </Tr>
          <Tr>
            <Td>{fallback(order?.verificationData?.country)}</Td>
            <Td>{fallback(order?.verificationData?.state)} </Td>
            <Td>{fallback(order?.verificationData?.cryptoWallet)}</Td>
          </Tr>
          <Tr>
            <Th>Submitter for verification</Th>
            <Th>Verification type</Th>
            <Th>Verification duration</Th>
          </Tr>
          <Tr>
            <Td>{fallback(order?.verificationData?.submittedAt)}</Td>
            <Td>{fallback(order?.verificationData?.verificationType)} </Td>
            <Td>{fallback(order?.verificationData?.verificationDuration)} </Td>
          </Tr>
          <Tr>
            <Th>Reject reason</Th>
            <Th>Verification decision</Th>
            <Th>Dossier ID</Th>
          </Tr>
          <Tr>
            <Td>{fallback(order?.verificationData?.rejectReason)} </Td>
            <Td>{fallback(order?.verificationData?.verificationDecision)} </Td>
            <Td>{fallback(order?.verificationData?.dossierId)}</Td>
          </Tr>
        </Tbody>
      </Table>

      <Table>
        <Caption>Payment data</Caption>
        <Tbody>
          <Tr>
            <Th>Deposit status</Th>
            <Th>Deposit amount </Th>
            <Th>Deposit details</Th>
          </Tr>
          <Tr>
            <Td>{fallback(order?.paymentData?.depositStatus)} </Td>
            <Td>
              {order?.paymentData?.depositAmount.amount}{" "}
              {order?.paymentData?.depositAmount.currency}
            </Td>
            <Td>{fallback(order?.paymentData?.depositDetails)} </Td>
          </Tr>

          <Tr>
            <Th>Deposit Id</Th>
            <Th>Provider fee</Th>
            <Th>Pay out status</Th>
          </Tr>
          <Tr>
            <Td>{fallback(order?.paymentData?.depositId)} </Td>
            <Td>{fallback(order?.paymentData?.providerFee)} </Td>
            <Td>{fallback(order?.paymentData?.payoutStatus)} </Td>
          </Tr>

          <Tr>
            <Th>Pay out amount</Th>
            <Th>Network fee</Th>
            <Th>Pay out id</Th>
          </Tr>
          <Tr>
            <Td>{fallback(order?.paymentData?.payoutAmount)} </Td>
            <Td>
              {order?.paymentData?.networkFee?.amount}{" "}
              {order?.paymentData?.networkFee?.currency}
            </Td>

            <Td>{fallback(order?.paymentData?.payoutId)} </Td>
          </Tr>

          <Tr>
            <Th>Actual network fee</Th>
            <Th>Transaction Id</Th>
            <Th>Merchant fee in set currency</Th>
          </Tr>
          <Tr>
            <Td>{fallback(order?.paymentData?.actualNetworkFee)} </Td>
            <Td>{fallback(order?.paymentData?.transactionId)} </Td>
            <Td>
              {order?.paymentData?.merchantFee.amount}{" "}
              {order?.paymentData?.merchantFee.currency}{" "}
            </Td>
          </Tr>

          <Tr>
            <Th>Direct fee</Th>
            <Th>Actual exchange rate</Th>
            <Th>FX</Th>
          </Tr>
          <Tr>
            <Td>
              {order?.paymentData?.directFee.amount}{" "}
              {order?.paymentData?.directFee.currency}
            </Td>
            <Td>{fallback(order?.paymentData?.actualExcangeRate)} </Td>
            <Td>{fallback(order?.paymentData?.fx)} </Td>
          </Tr>
        </Tbody>
      </Table>
    </Container>
  );
};
