import React, { useRef } from "react";

import { Button } from "components/Forms/Button";
import * as Modal from "components/Modal";
import { Portal } from "components/Portal";
import { Visible } from "components/Visible";

import { useOnClickOutside } from "hooks/useOnClickOutside";
import { useToggle } from "hooks/useToggle";

import { AddUserModalForm } from "./AddUserModalForm";

export const AddUserModal = () => {
  const modalContentRef = useRef();
  const [showModal, handleToggleModal] = useToggle();
  
  const handleShow = () => {
    handleToggleModal();
  }

  const handleClose = () => {
    handleToggleModal();
  }

  useOnClickOutside(modalContentRef, () => {
    if (showModal) {
      handleClose();
    }
  });

  return (
    <>
      <Button onClick={handleShow}>+ Add user</Button>
      <Visible if={showModal}>
        <Portal>
          <Modal.Overlay>
            <Modal.Content ref={modalContentRef}>
              <AddUserModalForm handleClose={handleClose} />
            </Modal.Content>
          </Modal.Overlay>
        </Portal>
      </Visible>
    </>
  );
};
