import React, { FC } from "react";
import { useHistory } from "react-router";

import { Button } from "components/Forms/Button";

import styles from "./NotFoundError.module.scss";

export interface INotFoundErrorProps {}

export const NotFoundError: FC<INotFoundErrorProps> = () => {
  const history = useHistory();

  const handleClick = () => {
    history.goBack();
  };

  return (
    <div className={styles.NotFoundError}>
      <p className={styles.message}>Page not round</p>
      <p>
        <Button variant="inverse" onClick={handleClick}>
          Go back
        </Button>
      </p>
    </div>
  );
};
