import moment from "moment";

export class RangeService {
  create(from: any, to: any, value: number | null = null) {
    let range = {};

    if (from && to) {
      const fromDate = moment(from);
      const toDate = moment(to);

      if (toDate.diff(fromDate, "days") <= 30) {
        range = this.daily(moment(from), moment(to), value);
      } else {
        range = this.monthly(moment(from), moment(to), value);
      }
    } else {
      range = this.monthly(
        moment().add(-1, "year"),
        moment().add(1, "day"),
        value
      );
    }

    return range;
  }

  diff(from: any, to: any) {
    if (from && to) {
      return moment(to).diff(moment(from), "days");
    }

    return 0;
  }

  daily(from: any, to: any, value: any = null): any {
    let range: any = [];
    let start = moment(to);
    let end = moment(from);

    while (start.diff(end) >= 0) {
      range.push(moment(start).format("YYYY-MM-DD"));
      start = start.add(-1, "day");
    }

    return range.reduce((reduction: any, current: any) => {
      reduction[current] = { date: current, value };
      return reduction;
    }, {});
  }

  monthly(from: any, to: any, value: any = null): any {
    let range: any = [];
    let start = moment(to);
    let end = moment(from);

    while (start.diff(end) >= 0) {
      range.push(moment(start).format("YYYY-MM-DD"));
      start = start.add(-1, "month");
    }

    return range.reduce((reduction: any, current: any) => {
      reduction[current] = { date: current, value };

      return reduction;
    }, {});
  }

  isBetween(date: any, from: any, to: any): boolean {
    return moment(date).isBetween(
      moment(from).add(0, "day"),
      moment(to).add(0, "day")
    );
  }

  initialize(from: any, to: any) {
    const hasRange = from && to;
    const start = moment(from).format("YYYY-MM-DD");
    const end   = moment(to).format("YYYY-MM-DD");

    return hasRange
      ? {
          [start]: {
            date: start,
            value: 0,
          },
          [end]: {
            date: end,
            value: 0,
          },
        }
      : {};
  }
}
