import React from "react";
import { observer } from "mobx-react-lite";

import { Table, Tbody, Td, Tfoot, Th, Thead, Tr } from "components/Table";
import * as Toggle from "components/Toggle";
import { useStore } from "providers/StoreProvider";

import { FunnelToggle } from "./FunnelToggle";

export const Panel = observer(() => {
  const { funnelStore } = useStore();

  return (
    <Table>
      <Thead>
        <Tr>
          <Th>
            <Toggle.Area>Stage</Toggle.Area>
          </Th>
          <Th>All orders</Th>
          <Th>All orders (%)</Th>
          <Th>Users</Th>
          <Th>Users (%)</Th>
          <Th>Volume(USD)</Th>
        </Tr>
      </Thead>

      <Tbody>
        {funnelStore.group.map(([group, items]: any) => (
          <FunnelToggle
            key={group.status}
            group={group}
            items={items}
            totalUsers={funnelStore.totalUsers}
            totalOrders={funnelStore.totalOrders}
          />
        ))}
      </Tbody>

      <Tfoot>
        <Tr>
          <Td>
            <Toggle.Area>Total</Toggle.Area>
          </Td>
          <Td>{""}</Td>
          <Td>{""}</Td>
          <Td>{""}</Td>
          <Td>{""}</Td>
          <Td>$ {funnelStore.totalVolume}</Td>
        </Tr>
      </Tfoot>
    </Table>
  );
});
