import { flow, makeAutoObservable } from "mobx";
import { RequestService } from "services/RequestService";

import { createError } from "utils/errors";

import { RootStore } from "./RootStore";

export class StatisticsStore {
  private readonly rootStore: RootStore;

  private readonly request: RequestService;

  public loading: boolean = false;

  public error: string = "";

  private data: any = [];

  get all() {
    return this.data;
  }

  get merchants() {
    return this.data.reduce((reduction: string[], { merchant }: any) => {
      if (merchant && !reduction.includes(merchant)) {
        reduction.push(merchant);
      }
      return reduction;
    }, []);
  }

  constructor(rootStore: RootStore, request: RequestService) {
    makeAutoObservable(this);

    this.rootStore = rootStore;

    this.request = request;
  }

  for(merchant: any = "") {
    if (merchant) {
      return this.all.find(({ merchant: id }: any) => id === parseInt(merchant));
    }

    return this.all.reduce(
      (reduction: any, current: any) => {
        const earning = parseFloat(current?.earning);
        const ordersAmount = parseFloat(current?.ordersAmount);
        const purchasesCount = parseInt(current?.purchasesCount, 10);

        if (earning) reduction.earning += earning;
        if (ordersAmount) reduction.ordersAmount += ordersAmount;
        if (purchasesCount) reduction.purchasesCount += purchasesCount;

        return reduction;
      },
      { earning: 0, ordersAmount: 0, purchasesCount: 0 }
    );
  }

  fetch = flow(function* (this: StatisticsStore, query: string) {
    try {
      this.loading = true;

      this.error = "";

      const res = yield this.request.get(
        `${process.env.REACT_APP_API_SERVICE_URL}/v3/dashboard/statistics?${query}`
      );

      if (res?.data) {
        this.data = res.data;
      } else {
        this.data = [];
      }
    } catch (error) {
      this.error = createError(error);
    } finally {
      this.loading = false;
    }
  });
}
