import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router } from "react-router-dom";
import "normalize.css";
import "./index.scss";

import { App } from "./containers/App";
import reportWebVitals from "./reportWebVitals";

if (process.env.REACT_APP_BUILD_CONFIGURATION === 'release') {
  // Release - is a full operation WITHOUT any delevelopment artifacts. In other words: IT IS FOR PRODUCTION
  ReactDOM.render(
    <React.StrictMode>
      <Router>
        <App />
      </Router>
    </React.StrictMode>,
    document.getElementById("root")
  );
} else if (process.env.REACT_APP_BUILD_CONFIGURATION === 'snapshot') {
  // Snapshot - is a full operation build with real API backend and allows to include devel features like performance tools, advanced logging etc.
  // This may is used
  // - ALWAYS, in evolution, presentation, test zones
  // - RARELY, in production zone to debug hard issues.
  ReactDOM.render(
    <React.StrictMode>
      <Router>
        <App />
      </Router>
    </React.StrictMode>,
    document.getElementById("root")
  );
} else if (process.env.REACT_APP_BUILD_CONFIGURATION === 'snapshot-mockup') {
  // Snapshot Mockup - is a backend less build for UI demo (uses mocked API Service)
  // This may is used as GitPages for the project
  ReactDOM.render(
    <React.StrictMode>
      <Router>
        <App />
      </Router>
    </React.StrictMode>,
    document.getElementById("root")
  );
} else if (process.env.REACT_APP_BUILD_CONFIGURATION === 'local') {
  // Local - is a build used in delevelopment purposes
  ReactDOM.render(
    <React.StrictMode>
      <Router>
        <App />
      </Router>
    </React.StrictMode>,
    document.getElementById("root")
  );
} else {
  throw new Error(`Unsupported velue of REACT_APP_BUILD_CONFIGURATION variable: ${process.env.REACT_APP_BUILD_CONFIGURATION}`);
}

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
