type Steps = { [code: number]: string };

export const STEP_EMAIL = 1;
export const STEP_VERIFICATION = 2;
export const STEP_PROCESSING = 3;
export const STEP_DONE = 4;

const steps: Steps = {
  [STEP_EMAIL]: "Step 1 Email",
  [STEP_VERIFICATION]: "Step 2 Verification",
  [STEP_PROCESSING]: "Step 3 Processing",
  [STEP_DONE]: "Step 4 Done",
};

export function getStepName(code: any): string | undefined {
  return steps[code];
}

export function hasStepCode(code: any): boolean {
  return steps[code] !== undefined;
}
