import React from "react";
import { observer } from "mobx-react-lite";
import { useHistory } from "react-router";

import { useQuery, useSortable, useQueryFilter } from "hooks";
import { useStore } from "providers/StoreProvider";

import { Link } from "components/Link";
import { Sortable } from "components/Sortable";
import { Table, Thead, Tbody, Tr, Th, Td, Tfoot } from "components/Table";
import * as Pagination from "components/Pagination";

import * as date from 'utils/date';

import { Filter } from './Filter';

export const Panel = observer(() => {
  const { orderListStore } = useStore();
  const query = useQuery();
  const queryFilter = useQueryFilter();
  const history = useHistory();
  
  const [sortable, handleSortable] = useSortable((field, order) => {
    orderListStore.sort(field, order);
  });

  const handleFieldSort = (field: string) => () => {
    handleSortable(field);
  };

  const hasPrevPage = orderListStore.hasPrevPage && (query.has("afterId") || query.has("beforeId"));
  const hasNextPage = orderListStore.hasNextPage;

  const handleBackward = () => {
    queryFilter.handlePrevPage(orderListStore.first?.id)
  };

  const handleForward = () => {
    queryFilter.handleNextPage(orderListStore.last?.id)
  };

  return (
    <>
      <Filter countries={orderListStore.countries} />

      <Table>
        <Thead>
          <Tr>
            <Th>
              <Sortable order={sortable["id"]} onClick={handleFieldSort("id")}>
                Order ID
              </Sortable>
            </Th>
            <Th>
              <Sortable
                order={sortable["createdAt"]}
                onClick={handleFieldSort("createdAt")}
              >
                Time
              </Sortable>
            </Th>
            <Th>
              <Sortable
                order={sortable["status"]}
                onClick={handleFieldSort("status")}
              >
                Status
              </Sortable>
            </Th>
            <Th>
              <Sortable
                order={sortable["amountCrypto.amount"]}
                onClick={handleFieldSort("amountCrypto.amount")}
              >
                Crypto amount
              </Sortable>
            </Th>
            <Th>
              <Sortable
                order={sortable["amountFiat.amount"]}
                onClick={handleFieldSort("amountFiat.amount")}
              >
                Fiat amount
              </Sortable>
            </Th>
            <Th>
              <Sortable
                order={sortable["userId"]}
                onClick={handleFieldSort("userId")}
              >
                User ID
              </Sortable>
            </Th>
          </Tr>
        </Thead>

        <Tbody>
          {orderListStore.all.map((order: any) => (
            <Tr key={order.id}>
              <Td>
                <Link to={`${history.location.pathname}/${order.id}`}>
                  {order.id}
                </Link>
              </Td>
              <Td>{date.format(order.createdAt)}</Td>
              <Td>{order.status}</Td>
              <Td>
                {order.amountCrypto.amount} {order.amountCrypto.currency}
              </Td>
              <Td>
                {order.amountFiat.amount} {order.amountFiat.currency}
              </Td>
              <Td>{order.userId}</Td>
            </Tr>
          ))}
        </Tbody>

        <Tfoot>
          <Tr>
            <Td colspan={6}>
              <Pagination.Area>
                <Pagination.Button
                  disabled={!hasPrevPage}
                  onClick={handleBackward}
                >
                  Prev page
                </Pagination.Button>
                <Pagination.Button
                  disabled={!hasNextPage}
                  onClick={handleForward}
                >
                  Next page
                </Pagination.Button>
              </Pagination.Area>
            </Td>
          </Tr>
        </Tfoot>
      </Table>
    </>
  );
});
