import React, { FC } from "react";

import { Button } from "components/Forms/Button";
import { IErrorProps } from "components/Errors/types";

import styles from "./NetworkError.module.scss";

export interface INetworkErrorProps extends IErrorProps {}

export const NetworkError: FC<INetworkErrorProps> = ({ children, error }) => {
  const reload = () => {
    window.location.reload();
  };

  return error ? (
    <div className={styles.NetworkError}>
      <p className={styles.message}>{error}</p>
      <p>
        <Button variant="inverse" onClick={reload}>
          Try again
        </Button>
      </p>
    </div>
  ) : (
    <>{children}</>
  );
};
