import { useQuery } from "./useQuery";

export function useQueryFilter() {
  const query = useQuery();

  return {
    get fromDate(): string {
      return query.has("fromDate") ? (query.get("fromDate") as string) : "";
    },
    get toDate(): string {
      return query.has("toDate") ? (query.get("toDate") as string) : "";
    },
    get placement(): string {
      return query.has("placement") ? (query.get("placement") as string) : "";
    },
    get merchant(): string {
      return query.has("merchant") ? (query.get("merchant") as string) : "";
    },
    get country(): string {
      return query.has("country") ? (query.get("country") as string) : "";
    },
    /**
     * 
     * @param merchant string
     */
    handleMerchant(merchant: string) {
      query.remove("placement");
      query.apply("merchant", merchant);
      query.refresh();
    },
    /**
     * 
     * @param placement string
     */
    handlePlacement(placement: string) {
      query.apply("placement", placement);
      query.refresh();
    },
    /**
     * 
     * @param from 
     * @param to 
     */
    handleDateRange(from = '', to = '') {
      query.apply("fromDate", from);
      query.apply("toDate", to);
      query.refresh();
    },
    /**
     * 
     * @param country string 
     */
    handleCountry(country: string) {
      query.apply("country", country);
      query.refresh();
    },
    /**
     * Navigate to the next page
     *
     * @param id string
     */
    handleNextPage(id: string) {
      query.remove("beforeId");
      query.apply("afterId", id);
      query.refresh();
    },
    /**
     * Navigate to the previous page
     *
     * @param id string
     */
    handlePrevPage(id: string) {
      query.remove("afterId");
      query.apply("beforeId", id);
      query.refresh();
    },
  };
}
