/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useRef, useLayoutEffect } from "react";
import moment from "moment";

import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

import styles from "./LineChart.module.scss";
import { useQueryFilter } from "hooks";

am4core.useTheme(am4themes_animated);

export interface ILineChart {
  name: string;
  data: any;
  style?: any;
}

export const LineChart = ({ name, data, style }: ILineChart) => {
  const { fromDate, toDate } = useQueryFilter();
  const from = fromDate ? moment(fromDate) : "";
  const to = toDate ? moment(toDate) : "";

  const chart: any = useRef(null);

  useLayoutEffect(() => {
    let x = am4core.create(name, am4charts.XYChart);

    x.data = data;

    // Create axes
    let dateAxis = x.xAxes.push(new am4charts.DateAxis());

    dateAxis.renderer.labels.template.fill = am4core.color("#344563");
    dateAxis.renderer.grid.template.stroke = am4core.color("#e3e7ee");
    dateAxis.renderer.grid.template.strokeOpacity = 1;

    if (from && to && to.diff(from, "days") <= 30) {
      dateAxis.renderer.minGridDistance = 30;
      dateAxis.dateFormats.setKey("day", "dd");
      dateAxis.periodChangeDateFormats.setKey("day", "dd");
      dateAxis.baseInterval = {
        timeUnit: "day",
        count: 1,
      };
    } else {
      dateAxis.dateFormats.setKey("month", "MMM");
      dateAxis.periodChangeDateFormats.setKey("month", "MMM");
      dateAxis.renderer.minGridDistance = 30;
      dateAxis.baseInterval = {
        timeUnit: "month",
        count: 1,
      };
    }

    let valueAxis = x.yAxes.push(new am4charts.ValueAxis());
    valueAxis.renderer.labels.template.fill = am4core.color("#344563");
    valueAxis.renderer.grid.template.stroke = am4core.color("#E3E7EE");
    valueAxis.renderer.grid.template.strokeOpacity = 1;
    valueAxis.renderer.baseGrid.stroke = am4core.color('#E3E7EE')

    // valueAxis.renderer.minGridDistance = 30;
    valueAxis.min = 0;
    valueAxis.extraMax = 0.4;
    valueAxis.paddingBottom = 20;

    // Create series
    let series: any = x.series.push(new am4charts.LineSeries());
    series.dataFields.valueY = "value";
    series.dataFields.dateX = "date";
    series.stroke = am4core.color("#1CB6C1");
    series.strokeWidth = 2;
    series.minBulletDistance = 10;
    series.tooltipText = "[bold]{date.formatDate()}:[/] {value}";
    // series.tooltip.disabled = true;
    series.tooltip.pointerOrientation = "vertical";

    // Add simple bullet
    var circleBullet = series.bullets.push(new am4charts.CircleBullet());
    circleBullet.circle.stroke = am4core.color("#fff");
    circleBullet.circle.fill = am4core.color("#1CB6C1");
    circleBullet.circle.strokeWidth = 2;

    // Add cursor
    x.cursor = new am4charts.XYCursor();
    x.cursor.xAxis = dateAxis;

    chart.current = x;

    return () => {
      x.dispose();
    };
  });

  return <div id={name} className={styles.LineChart} />;
};
