import React from "react";
import { observer } from "mobx-react-lite";
import { useForm, Controller } from "react-hook-form";

import { Button } from "components/Forms/Button";
import { FormControl } from "components/Forms/FormControl";
import { Input } from "components/Forms/Input";
import { Label } from "components/Forms/Label";
import { Multiselect } from "components/Forms/Multiselect";
import * as Modal from "components/Modal";

import { useStore } from "providers/StoreProvider";

import * as roles from "utils/roles";

import { resolver } from "./resolver";

export const AddUserModalForm = observer((props: any) => {
  const { handleClose } = props;
  const { memberStore } = useStore();

  const { control, formState, handleSubmit } = useForm({
    resolver,
    mode: "onChange",
  });

  const { isValid, errors } = formState;

  const disabled = !isValid || memberStore.loading;

  const onSubmit = async (payload: any) => {
    const roles = payload.roles.map(({ value }: any) => value);

    try {
      await memberStore.addNewUser({ ...payload, roles });

      await memberStore.fetch();
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <>
      <Modal.Header>
        <Modal.Title>Add new user to CexDirect</Modal.Title>
        <Modal.Subtitle>Fill user information to send invite</Modal.Subtitle>
      </Modal.Header>

      <Modal.Body>
        <FormControl>
          <Label>Email</Label>
          <Controller
            name="userEmail"
            control={control}
            defaultValue=""
            render={({ field: { value, onChange } }) => (
              <Input
                placeholder="Enter email"
                value={value}
                error={errors?.userEmail}
                onChange={onChange}
              />
            )}
          />
        </FormControl>

        <FormControl>
          <Label>Roles</Label>
          <Controller
            name="roles[]"
            control={control}
            render={({ field: { value, onChange } }) => (
              <Multiselect
                placeholder="Select role"
                values={[roles.SUPPORT, roles.FINANCE, roles.ADMINISTRATOR]}
                value={value}
                onChange={onChange}
              />
            )}
          />
        </FormControl>
      </Modal.Body>

      <Modal.Footer>
        <Button onClick={handleClose}>Cancel</Button>
        <Button
          variant="primary"
          disabled={disabled}
          onClick={handleSubmit(onSubmit)}
        >
          Add
        </Button>
      </Modal.Footer>
    </>
  );
});
