import React, { FC, SyntheticEvent, forwardRef } from "react";

import styles from "./Select.module.scss";

export interface ISelectProps {
  id?: string;
  name?: string;
  value?: string;
  defaultValue?: string;
  multiple?: boolean;
  onChange?: (evt: SyntheticEvent<HTMLSelectElement>) => void;
}

export interface OptionProps {
  id?: string;
  value?: string;
  disabled?: boolean;
}

export const Select: FC<ISelectProps> = forwardRef((props, ref: any) => {
  const { children, multiple = false, ...rest } = props;

  return (
    <div className={styles.wrapper}>
      <select {...rest} ref={ref} multiple={multiple}>
        {children}
      </select>
    </div>
  );
});

export const Option: FC<OptionProps> = (props) => {
  const { children, value, disabled } = props;

  return (
    <option className={styles.option} value={value} disabled={disabled}>
      {children}
    </option>
  );
};
