import { FC } from "react";

import { useProfile } from "providers/ProfileProvider";

interface IGuardProps {
  of: string[];
  fallback?: any;
}

export const Allow: FC<IGuardProps> = (props) => {
  const { children, of = [], fallback =  null } = props;

  const { roles } = useProfile();

  const allow = of.length > 0 && of.every((role: string) => {
    return roles.includes(role);
  });

  return allow ? children : fallback;
};

export const Forbid: FC<IGuardProps> = (props) => {
  const { children, of = [], fallback =  null } = props;

  const { roles } = useProfile();

  const forbid = of.length <= 0 || of.some((role: string) => {
    return roles.includes(role);
  });

  return forbid ? fallback : children;
};
