import React from "react";

import { Filter as FilterButton } from "components/Forms/Filter";
import { Filter as Component } from "components/pages/Orders/Filter";
import { FormControl } from "components/Forms/FormControl";
import { Select, Option } from "components/Forms/Select";
import { Visible } from "components/Visible";

import { useToggle } from "hooks/useToggle";
import { useQueryFilter } from "hooks/useQueryFilter";

import { getName as getCountryName } from "utils/countries";

interface IFilterProps {
  countries: string[];
}

export const Filter = ({ countries = [] }: IFilterProps) => {
  const queryFilter = useQueryFilter();
  const [visible, handleToggle] = useToggle(!!queryFilter.country);

  const handleToggleFilter = () => {
    if (queryFilter.country) {
     queryFilter.handleCountry('')
    }

    handleToggle();
  };

  const handleChange = (evt: React.FormEvent<HTMLSelectElement>) => {
    const { currentTarget } = evt;
    queryFilter.handleCountry(currentTarget?.value)
  };

  return (
    <Component>
      <FilterButton active={visible} onClick={handleToggleFilter} />

      <Visible if={visible}>
        <FormControl>
          <Select
            value={queryFilter.country}
            onChange={handleChange}
          >
            <Option value="">All countries</Option>
            {countries.map((code: string) => {
              const name = getCountryName(code);

              return (
                <Option key={code} value={code}>
                  {name ? name : code}
                </Option>
              );
            })}
          </Select>
        </FormControl>
      </Visible>
    </Component>
  );
};
