import React, { FC } from "react";

import cn from "classnames";

import styles from "./Sortable.module.scss";

const ASC = 1;
const DESC = -1;
const NORMAL = 0;

export const Sortable: FC<any> = ({ children, order = NORMAL, onClick }) => {
  const up = order >= ASC;
  const down = order <= DESC;

  return (
    <button
      onClick={onClick}
      className={cn(styles.Sortable, { [styles.up]: up, [styles.down]: down })}
    >
      {children}
    </button>
  );
};