import React, { FC, useMemo } from "react";

import { observer } from "mobx-react-lite";
import { useForm, Controller } from "react-hook-form";

import { Button } from "components/Forms/Button";
import { FormControl } from "components/Forms/FormControl";
import { Input } from "components/Forms/Input";
import { Label } from "components/Forms/Label";
import { Multiselect, map } from "components/Forms/Multiselect";

import * as Modal from "components/Modal";

import { useStore } from "providers/StoreProvider";

import * as roles from "utils/roles";

import { resolver } from "./resolver";

export interface IUpdateUserModalForm {
  user: any;
  handleClose: any;
}

export const UpdateUserModalForm: FC<IUpdateUserModalForm> = observer(
  ({ user, handleClose }) => {
    const { memberStore } = useStore();

    const { control, formState, handleSubmit } = useForm({
      resolver,
      mode: "onChange",
    });

    const { isValid } = formState;

    const disabled = !isValid || memberStore.loading;

    const selectedRoles = useMemo(() => {
      return user.permissions?.map(({ role }: any) => role);
    }, [user.permissions]);

    const onSubmit = async (payload: any) => {
      const roles = payload.roles.map(({ value }: any) => value);

      try {
        await memberStore.updateUser({ userEmail: user.userEmail, roles });

        await memberStore.fetch();
      } catch (err) {
        console.log(err);
      }
    };

    return (
      <>
        <Modal.Header>
          <Modal.Title>Update an existing user</Modal.Title>
          <Modal.Subtitle>Enter user info for {user.userEmail}</Modal.Subtitle>
        </Modal.Header>

        <Modal.Body>
          <FormControl>
            <Label>Email</Label>
            <Input disabled value={user.userEmail} />
          </FormControl>

          <FormControl>
            <Label>Roles</Label>
            <Controller
              name="roles[]"
              control={control}
              render={({ field: { value, onChange } }) => (
                <Multiselect
                  defaultValue={map(selectedRoles)}
                  placeholder="Select role"
                  values={[roles.SUPPORT, roles.FINANCE, roles.ADMINISTRATOR]}
                  value={value}
                  onChange={onChange}
                />
              )}
            />
          </FormControl>
        </Modal.Body>

        <Modal.Footer>
          <Button onClick={handleClose}>Cancel</Button>
          <Button
            variant="primary"
            disabled={disabled}
            onClick={handleSubmit(onSubmit)}
          >
            Update
          </Button>
        </Modal.Footer>
      </>
    );
  }
);
