/* eslint-disable react-hooks/exhaustive-deps */
import React, { FC, createContext, useEffect, useContext } from "react";
import { observer } from "mobx-react-lite";

import { NetworkError } from "components/Errors/NetworkError";
import { Loader } from "components/Loader";

import { useStore } from "providers/StoreProvider";

export const Context = createContext({ list: [], current: '' } as any);

export const MerchantProvider: FC = observer(({ children }) => {
  const { merchantStore } = useStore();

  useEffect(() => {
    if (merchantStore.isEmpty) {
      merchantStore.fetch();
    }
  }, []);

  return (
    <NetworkError error={merchantStore.error}>
      <Loader loading={merchantStore.loading}>
        <Context.Provider
          value={{ list: merchantStore.list, current: merchantStore.current }}
        >
          {children}
        </Context.Provider>
      </Loader>
    </NetworkError>
  );
});

export const useMerchants = (): any => useContext(Context);
