export const BTC = "BTC";
export const BCH = "BCH";
export const ETH = "ETH";
export const LTC = "LTC";
export const GUSD = "GUSD";
export const DASH = "DASH";
export const ONT = "ONT";
export const ONG = "ONG";

export function getColor(currency: string): string | undefined {
  const colors: { [currency: string]: string } = {
    [BTC]: "#f79319",
    [LTC]: "#345d9d",
    [BCH]: "#8dc351",
    [GUSD]: "#25ddf9",
    [DASH]: "#008ce7",
    [ETH]: "#838485",
    [ONT]: "#48a4ff",
    [ONG]: "#65b6ff",
  };

  return colors[currency];
}
