import React, { FC } from "react";
import cn from "classnames";

import styles from "./Info.module.scss";

export interface InfoProps {
  center?: boolean;
}

export const Info: FC<InfoProps> = ({ children, center }) => {
  return (
    <span className={cn(styles.info, { [styles.center]: center })}>
      {children}
    </span>
  );
};
