import React from "react";
import { observer } from "mobx-react-lite";

import * as Layout from "components/Chart";

import { useStore } from "providers/StoreProvider";

import { useQueryFilter } from "hooks/useQueryFilter";

import { Loader } from "components/Loader";

import * as date from "utils/date";

import { Chart } from "./Chart";

export const Purchases = observer(() => {
  const { statisticsStore, graphStore } = useStore();
  const { merchant, fromDate, toDate } = useQueryFilter();
  const statistics = statisticsStore.for(merchant);
  const from = date.from(fromDate);
  const to = date.from(toDate);
  const data = graphStore.purchases(merchant);
  const loading = statisticsStore.loading || graphStore.loading;

  return (
    <Layout.Container>
        <Loader loading={loading}>
      <Layout.Head>
        <Layout.Info>
          <Layout.Title>Purchases</Layout.Title>
          <Layout.Subtitle>{date.humanizeRange(from, to)}</Layout.Subtitle>
        </Layout.Info>
        <Layout.Value>
          {statistics?.purchasesCount ?? 0}
        </Layout.Value>
      </Layout.Head>
      <Layout.Content>
        <Chart data={data} />
      </Layout.Content>
      </Loader>
    </Layout.Container>
  );
});
