import { makeAutoObservable, flow } from "mobx";
import isEmpty from "lodash/isEmpty";

import { RequestService } from "services/RequestService";

import { createError } from "utils/errors";

import { RootStore } from "./RootStore";

export class MemberStore {
  private readonly rootStore: RootStore;

  private readonly request: RequestService;

  public loading: boolean = true;

  public error: string = "";

  private store: any = [];

  get isEmpty() {
    return isEmpty(this.store);
  }

  get items() {
    return this.store;
  }

  constructor(rootStore: RootStore, request: RequestService) {
    makeAutoObservable(this);

    this.rootStore = rootStore;

    this.request = request;
  }

  addNewUser = flow(function* (this: MemberStore, payload: any) {
    try {
      this.loading = true;

      this.error = "";

      const res = yield this.request.post(
        `${process.env.REACT_APP_API_SERVICE_URL}/v3/merchant/member`,
        payload
      );

      if (res?.data) {
        this.store = res.data;
      }
    } catch (error) {
      this.error = createError(error);
    } finally {
      this.loading = false;
    }
  });

  updateUser = flow(function* (this: MemberStore, payload: any) {
    try {
      this.loading = true;

      this.error = "";

      const res = yield this.request.post(
        `${process.env.REACT_APP_API_SERVICE_URL}/v3/merchant/member`,
        payload
      );

      if (res?.data) {
        this.store = res.data;
      }
    } catch (error) {
      this.error = createError(error);
    } finally {
      this.loading = false;
    }
  });

  fetch = flow(function* (this: MemberStore) {
    try {
      this.loading = true;

      this.error = "";

      const res = yield this.request.get(
        `${process.env.REACT_APP_API_SERVICE_URL}/v3/merchant/member`
      );

      if (res?.data) {
        this.store = res.data;
      }
    } catch (error) {
      this.error = createError(error);
    } finally {
      this.loading = false;
    }
  });
}
