import React, { FC, ReactNode } from "react";
import cn from "classnames";

import styles from "./Tr.module.scss";

export interface ITrProps {
  children: ReactNode;
  className?: string;
}

export const Tr: FC<ITrProps> = ({ children, className }) => {
  return <tr className={cn(styles.Tr, className)}>{children}</tr>;
};
