/* eslint-disable react-hooks/exhaustive-deps */
import React, {
  FC,
  createContext,
  useContext,
  useState,
  useEffect,
} from "react";

import { observer } from "mobx-react-lite";

import { NetworkError } from "components/Errors/NetworkError";
import { Loader } from "components/Loader";

import { useStore } from "providers/StoreProvider";

export const Context = createContext({} as any);

export const AuthProvider: FC = observer(({ children }) => {
  const { authStore } = useStore();

  const [state, setState] = useState();

  const loading = !state;

  useEffect(() => {
    async function loadUser() {
      const user: any = await authStore.authorize();

      if (user !== null) setState(user);
    }

    loadUser();
  }, []);

  return (
    <NetworkError error={authStore.error}>
      <Loader loading={loading}>
        <Context.Provider value={{ user: state }}>{children}</Context.Provider>
      </Loader>
    </NetworkError>
  );
});

export const useAuth = (): any => useContext(Context);
