import React, { FC, createContext, useContext } from "react";
import { StoreService, RootStore } from "services/StoreService";

export const Context = createContext(StoreService)

export const StoreProvider: FC = ({ children }) => {
  return <Context.Provider value={StoreService}>{children}</Context.Provider>;
};

export const useStore = (): RootStore => useContext(Context);
