import React, { SyntheticEvent, forwardRef } from "react";
import cn from "classnames";

import styles from "./Button.module.scss";

type ButtonVariants = "defaults" | "primary" | "group" | "inverse";

export interface IButtonProps {
  active?: boolean;
  className?: string;
  disabled?: boolean;
  variant?: ButtonVariants;
  type?: "button" | "submit" | "reset";
  onClick?: (evt: SyntheticEvent<HTMLButtonElement>) => void;
}

export const Button = forwardRef((props: any, ref: any) => {
  const { children, type, variant = "defaults", active, disabled, onClick } = props;
  const className = cn(props.className, styles.button, styles[variant],  {
    [styles.active]: active,
  });

  return (
    <button disabled={disabled} className={className} type={type} onClick={onClick} ref={ref}>
      {children}
    </button>
  );
});
