import { flow, makeAutoObservable, runInAction } from "mobx";
import values from "lodash/values";
import minBy from "lodash/minBy";
import sortBy from "lodash/sortBy";
import moment from "moment";
import { RequestService } from "services/RequestService";
import { RangeService } from "services/RangeService";

import * as currencies from "utils/currencies/crypto";

import { RootStore } from "./RootStore";

import { createError } from "utils/errors";

export class GraphStore {
  private readonly request: RequestService;

  private readonly rangeService: RangeService;

  private currencyGraph: any = [];

  private merchantGraph: any = [];

  public loading: boolean = false;

  public error: string = "";

  get items() {
    return this.merchantGraph ?? [];
  }

  get empty() {
    return this.merchantGraph.length <= 0;
  }

  get size() {
    return this.merchantGraph.length;
  }

  purchases(merchant = "") {
    const result = this.currencyGraph.reduce(
      (reduction: any, current: any) => {
        const { currency } = current;
        const count = parseInt(current?.orderCount, 10);

        const value = count > 0 ? count : 0;

        if (reduction[currency]) {
          reduction[currency].value += value;
        } else if (currency) {
          reduction[currency] = { value, currency };
        }

        return reduction;
      },
      {
        [currencies.BTC]: { currency: currencies.BTC, value: 0 },
        [currencies.BCH]: { currency: currencies.BCH, value: 0 },
        [currencies.ETH]: { currency: currencies.ETH, value: 0 },
        [currencies.LTC]: { currency: currencies.LTC, value: 0 },
        [currencies.GUSD]: { currency: currencies.GUSD, value: 0 },
        [currencies.DASH]: { currency: currencies.DASH, value: 0 },
      }
    );

    return values(result);
  }

  constructor(rootStore: RootStore, request: RequestService) {
    makeAutoObservable(this);

    this.request = request;

    this.rangeService = new RangeService();
  }

  private isMerchantsNotMatch(
    merchantFromFilter: any,
    merchantFromSource: any
  ) {
    return merchantFromFilter && merchantFromFilter !== merchantFromSource;
  }

  volume(merchant: string, from: any, to: any) {
    const range = this.merchantGraph.reduce((reduction: any, current: any) => {
      if (this.isMerchantsNotMatch(parseInt(merchant), current?.merchant)) {
        return reduction;
      }

      const { ordersAmount, timeframeStart: period } = current;

      const date = moment(period).format("YYYY-MM-DD");

      if (from && to && !this.rangeService.isBetween(date, from, to)) {
        return reduction;
      }

      const value = parseFloat(ordersAmount);

      if (reduction[date]) {
        reduction[date].value += value;
      } else {
        reduction[date] = { date, value };
      }

      return reduction;
    }, this.rangeService.initialize(from, to));

    let response = this.rangeService.create(from, to);

    if (!from && !to) {
      const min = minBy(values(range), ["date"]);
      if (min && min.date) {
        response = this.rangeService.create(min.date, moment());
      } else if (values(range).length <= 0) {
        response = this.rangeService.create(
          moment().add(-30, "days"),
          moment(),
          0
        );
      }
    }

    response = { ...response, ...range };

    response = sortBy(values(response), ["date"]);

    return response;
  }

  earnings(merchant: string, from: any, to: any) {
    const range = this.merchantGraph.reduce((reduction: any, current: any) => {
      if (this.isMerchantsNotMatch(parseInt(merchant), current?.merchant)) {
        return reduction;
      }

      const { earning, timeframeStart: period } = current;

      const date = moment(period).format("YYYY-MM-DD");

      const value = parseFloat(earning);

      if (reduction[date]) {
        reduction[date].value += value;
      } else {
        reduction[date] = { date, value };
      }

      return reduction;
    }, this.rangeService.initialize(from, to));

    let response = this.rangeService.create(from, to);

    if (!from && !to) {
      const min = minBy(values(range), ["date"]);
      if (min && min.date) {
        response = this.rangeService.create(min.date, moment());
      } else if (values(range).length <= 0) {
        response = this.rangeService.create(
          moment().add(-30, "days"),
          moment(),
          0
        );
      }
    }

    response = { ...response, ...range };

    response = sortBy(values(response), ["date"]);

    return response;
  }

  fetch = flow(function* (this: GraphStore, query: string = "") {
    try {
      this.loading = true;

      this.error = "";

      const res = yield this.request.get(
        `${process.env.REACT_APP_API_SERVICE_URL}/v3/dashboard/graph?points=24&${query}`
      );

      this.currencyGraph = res?.data?.currencyGraph ?? [];
      this.merchantGraph = res?.data?.merchantGraph ?? [];

    } catch (error) {
      this.error = createError(error);
      this.reset();
    } finally {
      this.loading = false;
    }
  });

  reset() {
    runInAction(() => {
      this.merchantGraph = [];
      this.currencyGraph = [];
    });
  }
}
