import React from "react";

import { routes } from "routes";

import { Aside } from "components/Aside";
import { Logo } from "components/Logo";
import * as Icons from "components/Icons";
import * as Nav from "components/Navigation";

import { WidgetGuard } from "containers/Guard/WidgetGuard";

export const Navigation = () => {
  return (
    <Aside>
      <Logo />
      <Nav.Area>
        <WidgetGuard>
          <Nav.Link exact to={routes.widget}>
            <Icons.Factory type="widget" />
            Widget 🔥
          </Nav.Link>
        </WidgetGuard>
        <Nav.Link
          to={routes.analytics}
          isActive={(match, { pathname }) =>
            pathname === routes.index || !!match
          }
        >
          <Icons.Factory type="analytics" /> Analytics
        </Nav.Link>
        <WidgetGuard>
          <Nav.Link exact to={routes.direct}>
            <Icons.Factory type="direct" />
            Direct
          </Nav.Link>
        </WidgetGuard>

        <Nav.Link to={routes.integration}>
          <Icons.Factory type="integration" />
          Integration by API
        </Nav.Link>

        <WidgetGuard>
          <Nav.Link to={routes.feedback}>
            <Icons.Factory type="feedback" />
            Leave your feedback
          </Nav.Link>
        </WidgetGuard>

        <Nav.Link to={routes.settings}>
          <Icons.Factory type="settings" />
          Settings
        </Nav.Link>

        <Nav.Link to={routes.logout}>
          <Icons.Factory type="logout" />
          Log out
        </Nav.Link>
      </Nav.Area>
    </Aside>
  );
};
