import { makeAutoObservable } from "mobx";

import { RequestService } from "services/RequestService";

import { AuthStore } from "./AuthStore";
import { ErrorStore } from "./ErrorStore";
import { FunnelStore } from "./FunnelStore";
import { GraphStore } from "./GraphStore";
import { MemberStore } from "./MemberStore";
import { MerchantStore } from "./MerchantStore";
import { OrderListStore } from "./OrderListStore";
import { OrderStore } from "./OrderStore";
import { ProfileStore } from "./ProfileStore";
import { SpecStore } from "./SpecStore";
import { StatisticsStore } from "./StatisticsStore";

export class RootStore {
  private readonly request: RequestService;

  public readonly authStore: AuthStore;

  public readonly errorStore: ErrorStore;

  public readonly funnelStore: FunnelStore;

  public readonly memberStore: MemberStore;

  public readonly merchantStore: MerchantStore;

  public readonly orderStore: OrderStore;

  public readonly orderListStore: OrderListStore;

  public readonly profileStore: ProfileStore;

  public readonly statisticsStore: StatisticsStore;

  public readonly specStore: SpecStore;

  public readonly graphStore: GraphStore;

  constructor() {
    makeAutoObservable(this);

    this.request = new RequestService();

    this.authStore = new AuthStore();

    this.errorStore = new ErrorStore();

    this.funnelStore = new FunnelStore(this, this.request);

    this.graphStore = new GraphStore(this, this.request);

    this.memberStore = new MemberStore(this, this.request);

    this.orderStore = new OrderStore(this, this.request);

    this.orderListStore = new OrderListStore(this, this.request);
    
    this.profileStore = new ProfileStore(this, this.request);

    this.merchantStore = new MerchantStore(this, this.request);

    this.statisticsStore = new StatisticsStore(this, this.request);

    this.specStore = new SpecStore(this, this.request);

    this.onAfterBootstrap();
  }

  onAfterBootstrap() {
    this.request.beforeRequest(async (config: any) => {
      try {
        const user = await this.authStore.user();

        if (user?.access_token) {
          config.headers["Authorization"] = `Bearer ${user?.access_token}`;
        }
      } catch (error) {
        console.error("Error while trying to extract an access_token", error);
      }

      try {
        const merchant = this.merchantStore.all[0];

        if (merchant?.id) {
          config.headers["cexdirect-impersonate-merchant-id"] = merchant.id;
        }
      } catch (error) {
        console.error("Error while trying to extract a merchant id", error);
      }

      return config;
    });

    this.request.afterRequest(
      async (response: any) => {
        return response;
      },
      async (error: any) => {
        if (error?.response?.status === 401) {
          await this.authStore.login();
        } else {
          throw error;
        }
      }
    );
  }
}
