import React, { FC, ReactNode } from "react";
import cn from "classnames";

import styles from "./Tfoot.module.scss";

export interface ITfootProps {
  children: ReactNode;
  className?: string;
}

export const Tfoot: FC<ITfootProps> = ({ children, className }) => {
  return <tfoot className={cn(styles.Tfoot, className)}>{children}</tfoot>;
};
