import React, { FC, ReactNode } from "react";
import cn from "classnames";

import styles from "./Td.module.scss";

export interface ITdProps {
  children: ReactNode;
  className?: string;
  colspan?: number;
  rowspan?: number;
  style?: any;
}

export const Td: FC<ITdProps> = ({ children, className, colspan, rowspan, style }) => {
  return (
    <td
      className={cn(styles.Td, className)}
      colSpan={colspan}
      rowSpan={rowspan}
      style={style}
    >
      {children}
    </td>
  );
};
