/* eslint-disable react-hooks/exhaustive-deps */
import { useState, useEffect } from "react";

export const useFlashMessage = (timeout: number): any => {
  const [state, setState] = useState("");

  useEffect(() => {
    let timer: any;

    if (state) {
      timer = setTimeout(() => {
        setState("");
        clearTimeout(timer);
      }, timeout);
    }

    return () => {
      clearTimeout(timer);
    };
  }, [state]);

  return [state, setState];
};
