import React, { FC } from "react";

import * as Guard from "containers/Guard";

import { ADMIN } from "utils/roles";

interface IAdminGuardProps {
  unless?: any;
}

export const AdminGuard: FC<IAdminGuardProps> = ({ children, unless = null}) => {
  const roles = [ADMIN];

  return (
    <Guard.Allow of={roles} fallback={unless}>
      {children}
    </Guard.Allow>
  );
};
