import React, { useRef, useLayoutEffect } from "react";

import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

import * as crypto from "utils/currencies/crypto";

am4core.useTheme(am4themes_animated);

export const Chart = ({ data }: any) => {
  let chart: any = useRef(null);

  useLayoutEffect(() => {
    const x = am4core.create("purchases", am4charts.XYChart);

    x.data = data;

    // Create axes
    let currencyAxis: any = x.xAxes.push(new am4charts.CategoryAxis());
    currencyAxis.dataFields.category = "currency";
    currencyAxis.renderer.minGridDistance = 30;
    currencyAxis.renderer.labels.template.horizontalCenter = "middle";
    currencyAxis.renderer.labels.template.verticalCenter = "middle";
    currencyAxis.renderer.labels.template.fill = am4core.color("#344563");
    currencyAxis.renderer.grid.template.location = 0;
    currencyAxis.renderer.grid.template.stroke = am4core.color("#E3E7EE");
    currencyAxis.renderer.grid.template.strokeOpacity = 0.9;

    let valueAxis = x.yAxes.push(new am4charts.ValueAxis());
    valueAxis.min = 0;
    valueAxis.paddingBottom = 20;
    valueAxis.renderer.labels.template.fill = am4core.color("#344563");
    valueAxis.renderer.grid.template.stroke = am4core.color("#E3E7EE");
    valueAxis.renderer.grid.template.strokeOpacity = 0.9;
    valueAxis.extraMax = 0.2;

    // Create series
    let series: any = x.series.push(new am4charts.ColumnSeries());
    series.sequencedInterpolation = true;
    series.dataFields.valueY = "value";
    series.dataFields.categoryX = "currency";
    series.columns.template.column.cornerRadiusTopLeft = 10;
    series.columns.template.column.cornerRadiusTopRight = 10;
    series.columns.template.column.fillOpacity = 0.8;
    series.columns.template.width = 100;
    series.columns.template.strokeWidth = 0;
    series.tooltipText = "[{categoryX}: bold]{valueY}[/]";
    series.tooltip.pointerOrientation = "vertical";

    series.columns.template.adapter.add(
      "fill",
      (fill: any, target: any) =>
        crypto.getColor(target.dataItem.categoryX) ??
        x.colors.getIndex(target.dataItem.index)
    );

    // Cursor
    x.cursor = new am4charts.XYCursor();
    x.cursor.behavior = "none";

    chart.current = x;

    return () => {
      x.dispose();
    };
  }, [data]);

  return <div id="purchases"></div>;
};
