import { makeAutoObservable, flow, runInAction } from "mobx";
import isEmpty from "lodash/isEmpty";
import get from "lodash/get";

import { RequestService } from "services/RequestService";

import { createError } from 'utils/errors';

import { RootStore } from "./RootStore";

const LIMIT = 10;

export class OrderListStore {
  private readonly rootStore: RootStore;

  private readonly request: RequestService;

  public loading: boolean = true;

  public error: string = "";

  private spec: any = {};

  private data: any[] = [];

  get isEmpty() {
    return !this.data.length;
  }

  get size() {
    return this.all.length;
  }

  get countries() {
    return this.spec?.countries ?? [];
  }

  get statuses() {
    return this.spec?.statuses ?? [];
  }

  get all() {
    return this.data;
  }

  get first(): any {
    return this.all.length ? this.all[0] : null;
  }

  get last(): any {
    return this.size ? this.all[this.size - 1] : null;
  }

  get hasPrevPage() {
    return this.all.length > 0;
  }

  get hasNextPage() {
    return this.all.length === 0 || this.all.length >= LIMIT;
  }

  constructor(rootStore: RootStore, request: RequestService) {
    makeAutoObservable(this);

    this.rootStore = rootStore;

    this.request = request;
  }

  sort(field: string, order: number) {
    this.data = this.data.slice().sort((a, b) => {
      const av = get(a, field);
      const bv = get(b, field);

      if (av > bv) {
        return 1 * order;
      }

      if (av < bv) {
        return -1 * order;
      }

      return 0;
    });
  }

  async fetchSpec() {
    if (isEmpty(this.spec)) {
      const res = await this.request.get(
        `${process.env.REACT_APP_API_SERVICE_URL}/v3/dashboard/order/spec`
      );

      runInAction(() => {
        if (res?.data) this.spec = res?.data;
      });
    }
  }

  async fetchData(query: string) {
    const res = await this.request.get(
      `${process.env.REACT_APP_API_SERVICE_URL}/v3/dashboard/order?limit=${LIMIT}&${query}`
    );

    runInAction(() => {
      if (res?.data) {
        this.data = res.data;
      }
    });
  }

  fetch = flow(function* (this: OrderListStore, query = "") {
    try {
      this.loading = true;
      this.error = "";
      yield Promise.all([this.fetchSpec(), this.fetchData(query)]);
    } catch (error) {
      this.error = createError(error);
    } finally {
      this.loading = false;
    }
  });
}
