/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import { observer } from "mobx-react-lite";

import { Error } from "components/Errors/Error";
import { Loader } from "components/Loader";

import { Filter } from "containers/Filter";

import { useStore } from "providers/StoreProvider";

import { useQuery } from "hooks/useQuery";
import { useQueryFilter } from "hooks/useQueryFilter";

import { Panel } from "./Panel";

export const Statistics = observer(() => {
  const { graphStore, statisticsStore, merchantStore, specStore } = useStore();
  const queryString = useQuery().toString();
  const queryFilter = useQueryFilter();

  useEffect(() => {
    statisticsStore.fetch(queryString);
  }, [queryString]);

  useEffect(() => {
    graphStore.fetch(queryString);
  }, [queryString]);

  return (
    <Error error={statisticsStore.error}>
      <Loader loading={statisticsStore.loading}>
        <Filter
          name="statistics"
          merchants={merchantStore.list}
          placements={specStore.getPlacements(queryFilter.merchant)}
        />
        <Panel />
      </Loader>
    </Error>
  );
});
