import React, { FC, SyntheticEvent, forwardRef } from "react";
import cn from "classnames";

import styles from "./Input.module.scss";

export interface InputProps {
  id?: string;
  placeholder?: string;
  name?: string;
  value?: string;
  disabled?: boolean;
  error?: string;
  type?: string;
  onChange?: (evt: SyntheticEvent<HTMLInputElement>) => void;
}

export const Input: FC<InputProps> = forwardRef((props, ref: any) => {
  const { children, error, ...rest } = props;

  return (
    <input
      ref={ref}
      className={cn(styles.Input, { [styles.error]: error })}
      {...rest}
    >
      {children}
    </input>
  );
});
