import React from "react";

import { useToggle } from "hooks";

import { Td, Tr } from "components/Table";
import * as Toggle from "components/Toggle";
import { Visible } from "components/Visible";

import { percents, round } from "./utils";

export const FunnelToggle = (props: any) => {
  const [open, onClick] = useToggle();
  const { group, items, totalUsers, totalOrders } = props;
  const { status, orders, users, volume } = group;

  return (
    <>
      <Tr key={`${status}`}>
        <Td>
          <Toggle.Area>
            <Toggle.Arrow open={open} onClick={onClick}>
              {status}
            </Toggle.Arrow>
          </Toggle.Area>
        </Td>
        <Td>{orders}</Td>
        <Td>{percents(orders, totalOrders)} %</Td>
        <Td>{users}</Td>
        <Td>{percents(users, totalUsers)} %</Td>
        <Td>$ {round(volume)}</Td>
      </Tr>

      <Visible if={open}>
        {items.map(({ status, orders, users, volume }: any) => (
          <Tr key={`${status}`}>
            <Td>
              <Toggle.Area>{status}</Toggle.Area>
            </Td>
            <Td>{orders}</Td>
            <Td>{percents(orders, totalOrders)} %</Td>
            <Td>{users}</Td>
            <Td>{percents(users, totalUsers)} %</Td>
            <Td>$ {round(volume)}</Td>
          </Tr>
        ))}
      </Visible>
    </>
  );
};
