import { flow, makeAutoObservable, runInAction } from "mobx";

import entries from "lodash/entries";
import sumBy from "lodash/sumBy";
import isArray from "lodash/isArray";
import isObject from "lodash/isObject";
import isEmpty from "lodash/isEmpty";

import { RequestService } from "services/RequestService";

import { createError } from "utils/errors";
import * as numbers from "utils/numbers";
import * as funnel from "utils/funnel";

import { RootStore } from "./RootStore";

type FunnelItem = {
  orders: number;
  status: string;
  step: number;
  users: number;
  volume: number;
};

export class FunnelStore {
  private readonly rootStore: RootStore;

  private readonly request: RequestService;

  public loading: boolean = true;

  public error: string = "";

  private data: FunnelItem[] = [];

  private specification: any = {};

  constructor(rootStore: RootStore, request: RequestService) {
    makeAutoObservable(this);

    this.rootStore = rootStore;

    this.request = request;
  }

  get isEmpty() {
    return !this.data.length;
  }

  get merchants() {
    const { merchants = [] } = this.specification;

    return merchants;
  }

  get total(): { orders: number; users: number; volume: number } {
    const of = { users: 0, orders: 0, volume: 0 };

    return this.data.reduce((acc: any, item: FunnelItem) => {
      const { users, orders, volume } = item;
      acc.users += users;
      acc.orders += orders;
      acc.volume += volume;

      return acc;
    }, of);
  }

  get totalUsers() {
    return this.total.users;
  }

  get totalOrders() {
    return this.total.orders;
  }

  get totalVolume() {
    return numbers.toMoney(this.total.volume);
  }

  get group() {
    const allowedFunnelSteps = {
      [funnel.STEP_EMAIL]: [],
      [funnel.STEP_VERIFICATION]: [],
      [funnel.STEP_PROCESSING]: [],
      [funnel.STEP_DONE]: [],
    };

    const group: { [step: number]: FunnelItem[] } = this.data.reduce(
      (reduction: any, current: any) => {
        const { step } = current;
        if (funnel.hasStepCode(step)) {
          (reduction[step] = reduction[step] ?? []).push(current);
        }
        return reduction;
      },
      allowedFunnelSteps
    );

    return entries(group).map(([code, items]) => {
      return [
        {
          status: funnel.getStepName(code),
          users: sumBy(items, "users"),
          orders: sumBy(items, "orders"),
          volume: sumBy(items, "volume"),
        },
        items,
      ];
    });
  }

  async fetchSpec() {
    if (isEmpty(this.specification)) {
      const res = await this.request.get(
        `${process.env.REACT_APP_API_SERVICE_URL}/v3/dashboard/funnel/spec`
      );

      runInAction(() => {
        this.specification = fromSpecApi(res?.data);
      });
    }
  }

  async fetchData(query: string = "") {
    const res = await this.request.get(
      `${process.env.REACT_APP_API_SERVICE_URL}/v3/dashboard/funnel?${query}`
    );

    runInAction(() => {
      this.data = fromFunnelApi(res?.data);
    });
  }

  /**
   *
   * @memberof FunnelStore
   */
  fetch = flow(function* (this: FunnelStore, query = "") {
    try {
      this.loading = true;
      this.error = "";

      yield this.fetchData(query);
    } catch (error) {
      this.error = createError(error);
    } finally {
      this.loading = false;
    }
  });
}

function fromSpecApi(data: any) {
  const defaults = { merchants: [] };

  return isObject(data) ? data : defaults;
}

function fromFunnelApi(data: any) {
  if (isArray(data)) {
    return data.map(({ status, step, usersQty, ordersQty, volume }) => ({
      step,
      status,
      users: usersQty,
      orders: ordersQty,
      volume: parseFloat(volume),
    }));
  }

  return [];
}
