import React, { FC } from "react";

import { Table, Thead, Tbody, Tr, Th, Td } from "components/Table";
import * as Toggle from "components/Toggle";
import { Capitalize } from "components/Capitalize";

import { Container } from "components/pages/Settings/Container";
import { Footer } from "components/pages/Settings/Footer";

import { AdminGuard } from "containers/Guard/AdminGuard";

import { AddUserModal } from "./AddUserModal";
import { UpdateUserModal } from "./UpdateUserModal";

export interface IPanelProps {
  items: any[];
}

export const Panel: FC<IPanelProps> = ({ items: members }) => {
  const renderMember = (member: any) => {
    const { permissions = [] } = member;

    const roles = permissions.map((permission: any) => permission?.role);

    return (
      <Tr key={member?.userEmail}>
        <Td>
          <AdminGuard unless={member?.userEmail}>
            <UpdateUserModal
              user={member}
              render={({ visible, onClick }) => (
                <Toggle.Area>
                  <Toggle.Arrow open={visible} onClick={onClick}>
                    {member?.userEmail}
                  </Toggle.Arrow>
                </Toggle.Area>
              )}
            />
          </AdminGuard>
        </Td>
        <Td>{member?.userConfirmed ? "Active" : "Disabled"} </Td>
        {/* <Td>{member?.createdAt ? member?.createdAt : "-"}</Td> */}
        {/* <Td>{member?.lastActivityAt ? member?.lastActivityAt : "-"}</Td> */}
        <Td>
          <Capitalize>{roles.join(", ")}</Capitalize>
        </Td>
      </Tr>
    );
  };

  return (
    <Container>
      <Table>
        <Thead>
          <Tr>
            <Th>
              <AdminGuard unless="Login">
                <Toggle.Area>Login</Toggle.Area>
              </AdminGuard>
            </Th>
            <Th>Status</Th>
            {/* <Th>Created at</Th> */}
            {/* <Th>Last activity at</Th> */}
            <Th>Access</Th>
          </Tr>
        </Thead>

        <Tbody>{members.map(renderMember)}</Tbody>
      </Table>

      <Footer>
        <AdminGuard>
          <AddUserModal />
        </AdminGuard>
      </Footer>
    </Container>
  );
};
