import axios, { AxiosInstance } from "axios";

export class RequestService {
  protected readonly instance: AxiosInstance;

  constructor() {
    this.instance = axios.create({
      baseURL: process.env.REACT_APP_API_SERVICE_URL,
    });
  }

  beforeRequest(onFulfilled?: <T>(config: T) => Promise<T>, onReject?: (error: any) => any) {
    this.instance.interceptors.request.use(onFulfilled, onReject);
  }

  afterRequest(onFulfilled?: <T>(response: T) => Promise<T>, onReject?: (error: any) => any) {
    this.instance.interceptors.response.use(onFulfilled, onReject);
  }


  async get(url: string): Promise<any> {
    return this.instance.get(url);
  }

  async post(url: string, data: object) {
    return this.instance.post(url, data)
  }
}
