import React from "react";
import { observer } from "mobx-react-lite";

import * as Layout from "components/Chart";

import { Loader } from "components/Loader";

import { useStore } from "providers/StoreProvider";

import { useQueryFilter } from "hooks/useQueryFilter";

import { LineChart } from "containers/charts/LineChart";

import * as date from "utils/date";
import * as numbers from "utils/numbers";

export const Volume = observer(() => {
  const { statisticsStore, graphStore } = useStore();
  const { merchant, fromDate, toDate } = useQueryFilter();
  const from = date.from(fromDate);
  const to = date.from(toDate);
  const data = graphStore.volume(merchant, from, to);
  const statistics = statisticsStore.for(merchant);

  const loading = statisticsStore.loading || graphStore.loading;

  return (
    <Layout.Container>
      <Loader loading={loading}>
        <Layout.Head>
          <Layout.Info>
            <Layout.Title>Volume (USD)</Layout.Title>
            <Layout.Subtitle>{date.humanizeRange(from, to)}</Layout.Subtitle>
          </Layout.Info>
          <Layout.Value>
            ${numbers.toMoney(statistics?.ordersAmount)}
          </Layout.Value>
        </Layout.Head>
        <Layout.Content>
          <LineChart name="volume" data={data} />
        </Layout.Content>
      </Loader>
    </Layout.Container>
  );
});
