/* eslint-disable no-restricted-globals */
import { makeAutoObservable, runInAction } from "mobx";
import { Log, User, UserManager } from "oidc-client";

import { createError } from 'utils/errors'

Log.logger = console;

Log.level = Log.INFO;

const IDENTITY_CLIENT_ID = 'CEXDirect.Dashboard';
const IDENTITY_REDIRECT_URI = '/profile/oauth-challenge/';
const IDENTITY_POST_LOGOUT_REDIRECT_URI = 'https://cexdirect.com';

console.log(process.env);
export class AuthStore {

  private readonly userManager: UserManager;

  public loading: boolean = true;

  public error: string = "";

  constructor() {
    makeAutoObservable(this);

    this.userManager = new UserManager({
      authority: process.env.REACT_APP_IDENTITY_SERVICE_URL,
      client_id: IDENTITY_CLIENT_ID,
      redirect_uri: `${window.location.origin}${IDENTITY_REDIRECT_URI}`,
      post_logout_redirect_uri: IDENTITY_POST_LOGOUT_REDIRECT_URI,
      response_type: "code",
      scope: "openid profile api",
      loadUserInfo: true,
      filterProtocolClaims: true,
    });
  }

  async user(): Promise<User | null> {
    try {
      return this.userManager.getUser();
    } catch (err) {
      this.handleError(err);
      return null;
    }
  }

  async login(): Promise<void> {
    try {
      await this.userManager.signinRedirect();
    } catch (err) {
      this.handleError(err);
    }
  }

  async authorize(): Promise<User | null> {
    try {
      let user = await this.userManager.getUser();

      if (user) return user;

      await this.userManager.signinRedirect();

      return this.userManager.getUser();

    } catch (err) {
      this.handleError(err);

      return null;
    }
  }

  async logout(): Promise<void> {
    await this.userManager.removeUser();
    await this.userManager.signoutRedirect();
  }

  handleError(error: Error): void {
    runInAction(() => {
      this.error = createError(error)
    });
  }
}
