import React from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import axios from "axios";

import { useFlashMessage } from "hooks";

import { Button } from "components/Forms/Button";
import { Form } from "components/Forms/Form";
import { FormControl } from "components/Forms/FormControl";
import { Input } from "components/Forms/Input";
import { Label } from "components/Forms/Label";
import { Select, Option } from "components/Forms/Select";
import { Error } from "components/Forms/Error";
import { Info } from "components/Forms/Info";

import { Footer as FormAction } from "./components/Footer";
import { Inner as FormFields } from "./components/Inner";
import { Section } from "./components/Section";
import { Title } from "./components/Title";

import { schema, Fields } from "./schema";

export const Booking = () => {
  const [infoMessage, flashInfoMessage] = useFlashMessage(2000);
  const [errorMessage, flashErrorMessage] = useFlashMessage(2000);
  const { register, reset, formState, handleSubmit } = useForm({
    mode: "onChange",
    resolver: zodResolver(schema),
  });

  const { errors, touchedFields: touched, isValid, isSubmitting } = formState;

  const disabled = !isValid || isSubmitting;

  const onSubmit = async (fields: Fields) => {
    try {
      await axios.post(
        `https://api.hsforms.com/submissions/v3/integration/submit/5657032/323f0d95-0d3b-47b8-900a-05afd63b8323`,
        {
          fields: [
            {
              name: "firstname",
              value: fields?.firstname,
            },
            {
              name: "lastname",
              value: fields?.lastname,
            },
            {
              name: "email",
              value: fields?.email,
            },
            {
              name: "company",
              value: fields?.company,
            },
            {
              name: "business_type",
              value: fields?.business_type,
            },
            {
              name: "country",
              value: fields?.country,
            },
          ],
        }
      );
      flashInfoMessage("Message successfully sent");
      reset({});
    } catch (err) {
      flashErrorMessage(err?.message);
    }
  };

  return (
    <Section>
      <Title>Book a DEMO</Title>

      <Form onSubmit={handleSubmit(onSubmit)}>
        <FormFields>
          <FormControl>
            <Label>First name</Label>
            <Input placeholder="Enter first name" {...register("firstname")} />
          </FormControl>

          <FormControl>
            <Label>Last name</Label>
            <Input placeholder="Enter last name" {...register("lastname")} />
          </FormControl>

          <FormControl>
            <Label>Email *</Label>
            <Input placeholder="Enter email" {...register("email")} />
            {touched?.email && errors?.email ? (
              <Error>{errors?.email?.message}</Error>
            ) : null}
          </FormControl>

          <FormControl>
            <Label>Company name</Label>
            <Input placeholder="Enter company name" {...register("company")} />
          </FormControl>

          <FormControl>
            <Label>Business type</Label>
            <Select {...register("business_type")} defaultValue="">
              <Option disabled={true} value="">
                Please Select
              </Option>
              <Option value="Cryptocurrency Exchange">
                Cryptocurrency Exchange
              </Option>
              <Option value="Gambling/Gaming">Gambling/Gaming</Option>
              <Option value="Wallet">Wallet</Option>
              <Option value="Forex/CFD/FX">Forex/CFD/FX</Option>
              <Option value="E-Commerce">E-Commerce</Option>
              <Option value="Other">Other</Option>
            </Select>
          </FormControl>

          <FormControl>
            <Label>Country/Region</Label>
            <Input placeholder="Enter country" {...register("country")} />
          </FormControl>
        </FormFields>

        <FormAction>
          <Button type="submit" variant="primary" disabled={disabled}>
            Book
          </Button>
        </FormAction>

        <FormControl>
          {errorMessage ? <Error center>{errorMessage}</Error> : null}
          {infoMessage ? <Info center>{infoMessage}</Info> : null}
        </FormControl>
      </Form>
    </Section>
  );
};
