import moment from "moment";

export const subdays = (days: number) => {
  return formatAsISOString(moment().add({ days }))
};

export const submonths = (months: number) => {
  return formatAsISOString(moment().add({ months }));
};

export const subyears = (years: number) => {
  return formatAsISOString(moment().add({ years }));
};

export const range = (from: any, to: any): string[] => {
  const range: string[] = [];

  if (!(from && to)) return range;

  const start = moment(from).utc(true);

  const end = moment(to).utc(true);

  const diff = Math.abs(start.diff(end, "days"));

  for (let i = 0; i < diff; i++) {
    range.push(
      formatAsRangeIndex(moment(start).add(i, "day").startOf("hours"))
    );
  }

  return range;
};

export function from(date: any): Date | null {
  try {
    return date ? new Date(date) : null;
  } catch (err) {
    console.warn(`Error while trying convert a date `, err?.message);
    return null;
  }
}

export const humanize = (start: any, end: any) => {
  if (start && end) {
    return moment.duration(moment(start).diff(moment(end))).humanize();
  }

  return "";
};

export const humanizeRange = (start: any, end: any) => {
  if (start && end) {
    return `From ${moment(start).format("DD/MM/YY")} - ${moment(end).format(
      "DD/MM/YY"
    )}`;
  }

  return "For all time";
};

export function format(date: any) {
  return moment(date).format("DD-MM-YY HH:mm:ss");
}

export function formatAsRangeIndex(date: any) {
  return moment(date).format("YYYY-MM-DD");
}

export function formatAsISOString(date: any) {
  return `${moment(date).format("YYYY-MM-DD")}T00:00:00.000Z`;
}

export function formatAsWeekDay(date: any) {
  return moment(date).utc(false).format("dd");
}

export function isEqual(d1: any, d2: any) {
  try {
    return moment(d1).isSame(moment(d2));
  } catch (err) {
    return false;
  }
}
