import React from "react";
import { Switch, Route } from "react-router-dom";

import { routes } from "routes";

import { Container } from "components/pages/Analytics/Container";
import * as Nav from "components/pages/Analytics/Nav";

import { Statistics } from "pages/Statistics";
import { Funnel } from "pages/Funnel";
import { Orders } from "pages/Orders";
import { Order } from "pages/Order";

import { SpecProvider } from "providers/SpecProvider";
import { useQuery } from "hooks/useQuery";

export const Analytics = () => {
  const querySearch = useQuery().toSearch();

  return (
    <SpecProvider>
      <Container>
        <Nav.Area>
          <Nav.Link
            exact
            to={`${routes.analytics}${querySearch}`}
            isActive={(match, { pathname }) => pathname === routes.index ||!!match}
          >
            Dashboard
          </Nav.Link>
          <Nav.Link exact to={`${routes.funnel}${querySearch}`}>
            Funnel
          </Nav.Link>
          <Nav.Link exact={false} to={`${routes.orders}${querySearch}`}>
            Orders
          </Nav.Link>
        </Nav.Area>
          <Switch>
            <Route exact path={routes.order} component={Order} />
            <Route>
              <Switch>
                <Route exact path={[routes.index, routes.analytics]} component={Statistics} />
                <Route exact path={routes.funnel} component={Funnel} />
                <Route exact path={routes.orders} component={Orders} />
              </Switch>
            </Route>
          </Switch>
      </Container>
    </SpecProvider>
  );
};
