import { makeAutoObservable, flow } from "mobx";
import isEmpty from "lodash/isEmpty";

import { RequestService } from "services/RequestService";

import { createError } from "utils/errors";

import { RootStore } from "./RootStore";

interface IPlacement {
  [k: string]: string[];
}

interface ISpec {
  merchants?: string[];
  placements?: IPlacement;
}

export class SpecStore {
  private readonly rootStore: RootStore;

  private readonly request: RequestService;

  public loading: boolean = true;

  public error: string = "";

  private data: ISpec = {};

  get isEmpty() {
    return isEmpty(this.data);
  }

  protected get placements(): any {
    return this.data?.placements ?? [];
  }

  constructor(rootStore: RootStore, request: RequestService) {
    makeAutoObservable(this);

    this.rootStore = rootStore;

    this.request = request;
  }

  getPlacements(merchant: string) {
    return merchant
      ? this.placements.filter((placement: any) => {
          return placement.merchant === parseInt(merchant);
        })
      : [];
  }

  fetch = flow(function* (this: SpecStore) {
    try {
      this.loading = true;
      this.error = "";

      const result = yield this.request.get(
        `${process.env.REACT_APP_API_SERVICE_URL}/v3/dashboard/funnel/spec`
      );

      if (result?.data) this.data = result.data;
    } catch (error) {
      this.error = createError(error);
    } finally {
      this.loading = false;
    }
  });
}
