/* eslint-disable react-hooks/exhaustive-deps */
import React, { FC, createContext, useEffect, useContext } from "react";
import { observer } from "mobx-react-lite";

import { NetworkError } from "components/Errors/NetworkError";
import { Loader } from "components/Loader";

import { useStore } from "providers/StoreProvider";

export const Context = createContext({ roles: [] } as any);

export const ProfileProvider: FC = observer(({ children }) => {
  const { profileStore } = useStore();

  useEffect(() => {
    if (profileStore.isEmpty) {
      profileStore.fetch();
    }
  }, []);

  return (
    <NetworkError error={profileStore.error}>
      <Loader loading={profileStore.loading}>
        <Context.Provider value={{ roles: profileStore.roles }}>
          {children}
        </Context.Provider>
      </Loader>
    </NetworkError>
  );
});

export const useProfile = (): any => useContext(Context);
