import React from "react";

// import isEqual from "lodash/isEqual";

import { Button } from "components/Forms/Button";
import { Datepicker } from "components/Datepicker";
import { Group } from "components/Forms/Group";
import * as Component from "components/Filter";
import * as Icons from "components/Icons";

import * as date from "utils/date";

import { useQueryFilter } from "hooks/useQueryFilter";

export const Date = () => {
  const { fromDate, toDate, handleDateRange } = useQueryFilter();

  const enabled = fromDate && toDate;

  const today = date.subdays(0);
  const yesterday = date.subdays(-1);
  const lastWeek = date.subdays(-6);
  const lastMonth = date.subdays(-30);
  const lastHalfYear = date.submonths(-6);
  const lastYear = date.subyears(-1);

  const ifToday = date.isEqual(today, toDate);
  const ifYesterday = date.isEqual(yesterday, fromDate) && ifToday;
  const ifLastWeek = date.isEqual(lastWeek, fromDate) && ifToday;
  const ifLastMonth = date.isEqual(lastMonth, fromDate) && ifToday;
  const ifHalfYear = date.isEqual(lastHalfYear, fromDate) && ifToday;
  const ifLastYear = date.isEqual(lastYear, fromDate) && ifToday;
  const ifCustom = enabled && !(ifYesterday || ifLastWeek || ifLastMonth || ifHalfYear || ifLastYear);

  const handleChange = (from = '', to = '') => {
    if (from && to) {
      return handleDateRange(
        date.formatAsISOString(from), 
        date.formatAsISOString(to)
      );
    }
    return handleDateRange();
  };

  return (
    <Component.Range>
      <Group>
        <Datepicker
          toDate={date.from(toDate)}
          fromDate={date.from(fromDate)}
          onChange={handleChange}
        >
          <Button variant="group" active={ifCustom}>
            <Icons.Factory type="calendar" />
            Custom
          </Button>
        </Datepicker>

        <Button
          variant="group"
          active={ifYesterday}
          onClick={() => handleChange(yesterday, today)}
        >
          Yesterday
        </Button>
        <Button
          variant="group"
          active={ifLastWeek}
          onClick={() => handleChange(lastWeek, today)}
        >
          7D
        </Button>
        <Button
          variant="group"
          active={ifLastMonth}
          onClick={() => handleChange(lastMonth, today)}
        >
          30D
        </Button>
        <Button
          variant="group"
          active={ifHalfYear}
          onClick={() => handleChange(lastHalfYear, today)}
        >
          6M
        </Button>
        <Button
          variant="group"
          active={ifLastYear}
          onClick={() => handleChange(lastYear, today)}
        >
          12M
        </Button>
        <Button
          variant="group"
          active={!enabled}
          onClick={() => handleChange()}
        >
          All
        </Button>
      </Group>
    </Component.Range>
  );
};
