import React, { FC } from "react";
import cn from "classnames";

import styles from "./Error.module.scss";

export interface IErrorProps {
  center?: boolean;
}

export const Error: FC<IErrorProps> = ({ children, center }) => {
  return (
    <span className={cn(styles.error, { [styles.center]: center })}>
      {children}
    </span>
  );
};
