import { flow, makeAutoObservable } from "mobx";
import toLower from 'lodash/toLower';
import { RequestService } from "services/RequestService";

import { createError } from "utils/errors";

import { RootStore } from "./RootStore";

export class MerchantStore {
  private readonly rootStore: RootStore;

  private readonly request: RequestService;

  public loading: boolean = true;

  public error: string = "";

  protected merchants: any[] = [];

  constructor(rootStore: RootStore, request: RequestService) {
    makeAutoObservable(this);

    this.rootStore = rootStore;

    this.request = request;
  }

  get isEmpty() {
    return !this.merchants.length;
  }

  get all() {
    return this.merchants;
  }

  get list() {
    return flatten(this.merchants)
      .filter(({ children }: any) => children.length <= 0)
      .map(({ id, name }: any) => ({ id, name: toLower(name) }));
  }

  get current() {
    return this.isEmpty ? null : this.all[0];
  }

  fetch = flow(function* (this: MerchantStore) {
    try {
      this.error = "";
      this.loading = true;

      const res = yield this.request.get(
        `${process.env.REACT_APP_API_SERVICE_URL}/v3/merchant`
      );

      if (res?.data) {
        this.merchants = res.data;
      }
    } catch (err) {
      this.error = createError(err);
    } finally {
      this.loading = false;
    }
  });
}

function flatten(merchants: any[]) {
  const result: any = [];

  for (const merchant of merchants) {
    const { children } = merchant;

    if (children.length) result.push(...flatten(children));

    result.push(merchant);
  }

  return result;
}
